@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

/*###########################################################################
# +-----------------------------------------------------------------------+ #
# |                       Univarsal CSS                                   | #
# +-----------------------------------------------------------------------+ #
###########################################################################*/
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Poppins', sans-serif;
	font-weight: 40;
}

/*  Theme System */
:root {
	--background-color: #fff;
	--navigation-color: #fff;
	--theme-color: #fff;
}

.light {
	--background-color: rgb(255, 255, 255);
	--navigation-color: rgba(0, 0, 0, 0.11);
	--theme-color: rgba(0, 0, 0, 0.253);
	--text-color: rgba(0, 0, 0, 0.699);
	--menubar-color: rgba(0, 0, 0, 0.062);
	--menubar-active-color: rgba(0, 0, 0, 0.699);
	--profile-menu-color: rgba(34, 34, 34, 0.185);
	--menubar-li-hover-color: rgb(0, 102, 255);
	--menubar-li-hover-color-lite: rgba(56, 121, 218, 0.993);
	--menubar-menu-icon-active-color: rgb(162, 0, 255);
	--menubar-logout-icon-hover-color: rgba(255, 255, 255, 0);
}

.dark {
	--background-color: rgb(0, 0, 0);
	--navigation-color: rgba(255, 255, 255, 0.11);
	--theme-color: rgba(255, 255, 255, 0.253);
	--text-color: rgb(255, 255, 255);
	--menubar-color: rgba(255, 255, 255, 0.062);
	--menubar-active-color: rgb(255, 255, 255);
	--profile-menu-color: rgba(255, 255, 255, 0.226);
	--menubar-li-hover-color: rgb(0, 102, 255);
	--menubar-li-hover-color-lite: rgba(110, 168, 255, 0.993);
	--menubar-menu-icon-active-color: rgb(162, 0, 255);
	--menubar-logout-icon-hover-color: rgba(255, 255, 255, 0);
}
body
{
	background-color: var(--background-color);
}

