.login {
	background: linear-gradient(287.56deg, #0F0C29 0%, #302B63 50%, #24243E 100%);
	width: 100%;
	height: 100%;
	justify-content: center;
	align-items: center;
	display: flex;
	/* overflow: hidden; */
}

.login .box {
	position: relative;
	width: 490px;
	height: 600px;
	background: rgba(255, 255, 255, 0.1);
	backdrop-filter: blur(2.6667px);
	border-radius: 40px;
	display: block;
	justify-content: center;
	align-items: center;
	z-index: 1;
	padding: 1px;
	border: 5px solid rgba(255, 255, 255, 0.295);
}

.login .box .form {
	padding: 0px;
	position: relative;
	top: 3vh;
	left: 1vw;
	width: 461px;
	height: 448px;
}

.login .box .form h2 {
	margin-top: 15px;
	margin-left: 15px;
	position: relative;
	height: 55px;
	font-weight: 700;
	font-size: 38px;
	display: flex;
	align-items: center;
	color: #FFFFFF;
	pointer-events: none;
}

/* Email */
.login .box .form .Input_Email {
	display: block;
