	font-weight: 400;
	font-size: 15px;
	line-height: 21px;
	display: flex;
	align-items: center;
	color: #FFFFFF;
}

.login .box .form .Input_Email .email {
	margin-top: 5px;
	position: relative;
	width: 432px;
	height: 45px;
	background: #FFFFFF;
	border: 1px solid #BCBEC0;
	border-radius: 10px;
	padding-left: 15px;
	outline: none;
}

/* Password */
