.ui .theme {
	border-radius: 50%;
	/* Add Heare Medeia Query TO Make Responsive */
	width: 50px;
	height: 50px;
	position: fixed;
	bottom: 40px;
	right: 40px;
	z-index: 10;
}
.ui .theme img {
	width: 100%;
	height: 100%;
}

.uitest .uibody {
	transition: 0.5s;
	background: linear-gradient(112.68deg, rgba(242, 78, 117, 0.25) 12.5%, rgba(1, 125, 240, 0.0525) 100%), #191A1E;
	overflow: hidden;
	display: flex;
}

/* ? Menubar */
.uitest .menubar
{
	position: relative;
	width: 300px;
	background: rgba(0, 0, 0, 0.3);
	height: 100vh;
	overflow: auto;
}
.uitest .menubar::-webkit-scrollbar
{
	width: 0px;
}
.uitest .menubar .profile
{
	padding-top: 40px;
	/* margin-top: 40px; */
	position: relative;
	width:100%;
	height:240px;
	display: block;
	justify-content: center;
	align-items: center;
}
.uitest .menubar .profile .img
{
	margin-left: 30%;
	position: relative;
	width:120px;
	height:120px;
	border-radius: 50%;
}
.uitest .menubar .profile .img img
{
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: 50%;
}
.uitest .menubar .profile .username
{
	margin-top: 10px;
	position: relative;
	width:100%;
	height:35px;
	color: white;
	font-size: 25px;
	font-family: 'Poppins';
	justify-content: center;
	display: flex;
	align-items: center;
}
/* ?  Menubar Menu */
.uitest .menubar .menu
{
	position: relative;
	display: block;
	justify-content: center;
	align-items: center;
	width: auto;
	height: 100%;
	overflow-y:auto;
	overflow-x: hidden;
}
.uitest .menubar .menu .header
{
	margin: 20px 0px 5px 0px;
	padding-left: 40px;
	position: relative;
	color: white;
	font-size: 15px;
}
.uitest .menubar .menu .item
{
	margin: 5px 0px 15px 0px;
	height: 40px;
	padding-left: 30px;
	position: relative;
	display: flex;
	align-items: center;
	color: white;
}

.uitest .menubar .menu NavLink
{
	text-decoration: none;
}
.uitest .menubar .menu .item
{
	position: relative;
	margin: 15px 0px 15px 0px;
	height: 40px;
	padding-left: 30px;
	display: flex;
	align-items: center;
	color: white;
	text-decoration: none !important;
}
.uitest .menubar .menu .active
{
	text-decoration: none;
}
.uitest .menubar .menu .notactive
{
	text-decoration: none;
}
.uitest .menubar .menu .active .item
{
	background: linear-gradient(270deg, rgba(255, 255, 255, 0.75) -1.28%, rgba(255, 255, 255, 0.15) 100%);
}
.uitest .menubar .menu .active .item::after
{
	content: '';
	background-color: #ffffff;
	width:5px;
	position: absolute;
	height: 45px;
	left: 297px;
	border-radius: 30px 0px 0px 30px;
}
.uitest .menubar .menu .item .icon
{
	margin-left: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
}
.uitest .menubar .menu .item .title
{
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	margin-left: 15px;
	color: white;
	opacity: 0.7;
}
.uitest .menubar .menu .active .item .title
{
	opacity: 1;
}
/*############################################################################
# +------------------------------------------------------------------------+ #
# |                            UI Component                                | #
# +------------------------------------------------------------------------+ #
############################################################################*/
.ui .navigation

{
	position: fixed;
	top: 25px;
	right: 25px;
	left: 25px;
	bottom: 25px;
	overflow: hidden;
	border-radius: 20px;
}
.ui .navigation .menubar {
	width: 80px;
	position: absolute;
	top: 60px;
	left: 0px;
	z-index: 1;
	bottom: 0px;
	transition: 0.5s;
}

.ui .navigation .menubar:hover,
.menubar_active {
	display: block;
	width: 155px;
	position: absolute;
	top: 60px;
	left: 0px;
	z-index: 1;
	bottom: 0px;
	transition: 0.5s;
}

.ui .navigation .menu_ul {
	position: relative;
	top: 0px;
	left: 0px;
	height: 100%;
	bottom: 0px;
	list-style: none;
	overflow-y: auto;
	overflow-x: hidden;
	padding: 2px;
	color: var(--text-color);
	background-color: var(--menubar-color);
	display: block;
}

.ui li {
	margin-top: 10px;
	position: relative;
	gap: 10px;
	display: flex;
	top: 60px;
	left: 15px;
	padding: 10px;
	text-decoration: none;
}

.ui .menubar .menu_text {
	opacity: 0;
}

.ui .menubar_active .menu_text,
.ui .menubar:hover .menu_text {
	opacity: 1;
}

.ui .menu_img {
	position: absolute;
	width: 50px;
	height: 50px;
	margin: 5px;
	left: 10px;
	top: 10px;
}

.ui .menubar_active .menu_img {
	color: var(--menubar-menu-icon-active-color);
}

.ui .navigation .menu_li .active {
	color: var(--menubar-li-hover-color);
	text-decoration: none;
	display: flex;
	gap: 10px;
}

.ui .navigation .menu_li .notactive {
	color: var(--text-color);
	text-decoration: none;
	display: flex;
	gap: 10px;
}

.ui .navigation .menu_li .notactive:hover {
	color: var(--menubar-li-hover-color-lite);
	text-decoration: none;
	display: flex;
	gap: 10px;
}

.ui .menu_icon {
	width: 100%;
	height: 100%;
}

.ui .menu_li .menu_logo {
	width: 27px;
	height: 27px;
}

.ui .menu_li .menu_logo svg {
	width: 27px;
	height: 27px;
}

.ui .logout .menu_logo {
	width: 27px;
	height: 27px;
}

.ui .logout .menu_logo svg {
	width: 27px;
	height: 27px;
}

.ui .menu_text {
	position: relative;
	top: 5px;
}

.ui .logout {
	position: absolute;
	top: calc(100% - 90px);
}

.ui .navigation .statusbar {
	position: relative;
	right: 25px;
	height: 60px;
	background-color: var(--navigation-color);
	left: 0px;
}

.ui .navigation .statusbar .profile {
	position: relative;
	left: calc(100% - 60px);
	width: 45px;
	height: 45px;
	top: 7px;
	border-radius: 50%;
}

.ui .navigation .statusbar .profile img {
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: 50%;
}

.ui .statusbar .profile_toggle {
	display: none;
	opacity: 0;
}

.ui .statusbar .profile_toggle_visible {
	position: fixed;
	display: flex;
	opacity: 1;
	background-color: var(--profile-menu-color);
	width: 300px;
	height: 300px;
	z-index: 2;
	top: 90px;
	left: calc(100% - 330px);
	border-radius: 30px;
}

.ui .statusbar .profile_toggle_visible .profile_img_area {
	position: fixed;
	width: 260px;
	top: 100px;
	left: calc(100% - 310px);
}

.ui .statusbar .profile_toggle_visible .profile_img {
	position: relative;
	width: 150px;
	height: 150px;
	border-radius: 50%;
	align-items: center;
	left: 80px;
	top: 20px;
}

.ui .statusbar .profile_toggle_visible .profile_img img {
	position: fixed;
	width: 100px;
	height: 100px;
	border-radius: 50%;
}

.ui .statusbar .profile_toggle_visible .account_name {
	position: fixed;
	width: 200px;
	top: 230px;
	left: calc(100% - 270px);
	text-overflow: ellipsis;
	color: var(--text-color);
	font-size: 20px;
}

.ui .statusbar .profile_toggle_visible .profile_ul {
	padding: 5px 5px 5px 5px;
	position: fixed;
	width: 280px;
	height: 175px;
	top: 300px;
	left: calc(100% - 320px);
}

.ui .statusbar .profile_toggle_visible li {
	margin-top: 0px;
	position: relative;
	width: 260px;
	height: 50px;
	top: 0;
	font-size: 15px;
	font-weight: 600;
	color: var(--text-color);
	left: 5px;
}

.ui .mbody {
	position: relative;
	top: 85px;
	left: 105px;
	width: calc(100% - 130px);
	height: calc(100% - 110px);
	z-index: 1;
	overflow-y: auto;
	overflow-x: hidden;
	border-radius: 20px;
}

